<template>
   <div>
      <el-row>
         <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
               prop="treeCode"
               label="树木编码">
               <template slot-scope="scope">
                  <router-link :to="{name:'tree', params:{ treeCode: scope.row.treeCode }}">
                  <el-button type="text" size="small">{{scope.row.treeCode}}</el-button>
                  </router-link>
               </template>
            </el-table-column>
            <el-table-column
               prop="treeType"
               label="树木种类">
            </el-table-column>
            <el-table-column
               prop="measureTimes"
               label="测量次数">
            </el-table-column>
            <el-table-column
               prop="grow"
               label="生长速度(cm/年)">
            </el-table-column>
         </el-table>
      </el-row>
   </div>
</template>

<script>
import conf from '../conf.js';
export default {
   data(){
      return {
         tableData:[{
            treeCode:'tree1111',
            treeType:'未知',
            measureTimes:10,
            grow:12.1,
         },{
            treeCode:'tree1111',
            treeType:'未知',
            measureTimes:10,
            grow:12.1,
         }]
      }
   },
   mounted(){
      let self = this;
      fetch(`${conf.ROOT}/api/trees`)
         .then(res => res.json())
         .then(res => {
            self.tableData = res.data;
         })
   }
}
</script>

<style>

</style>



