<template>
   <div>
      <el-row>
         <el-input v-model="treeCode" placeholder="请输入树木编码" style="width:250px;"></el-input>
         <el-button type="primary" @click="search">搜索</el-button>   
         <!-- <el-button v-show="tableData.length>0">下载csv统计文件<i class="el-icon-download el-icon--right"></i></el-button>          -->
      </el-row>
      <el-row class="info">
          <span>树种：未知</span>
          <span>测量次数：{{treeInfo.measureTimes}}</span>
          <span>生长速度：{{treeInfo.grow}} cm/年</span>
      </el-row>
      <div id="chart"></div>
      <z-table :tableData="tableData"></z-table>      
   </div>
</template>

<script>
import conf from '../conf.js';
import Table from './self/Table.vue';
export default {
    components:{
        'z-table':Table
    },
   data(){
      return {
         myChart:null,
         treeCode:'',
         tableData:[],
         treeInfo:{
            measureTimes:0,
            grow:0
         }
        //  tableData:[{
        //     time: '2016-05-02',
        //     treeCode: 'tree111',
        //     diameterCalculate: 11.1,
        //     pic:'../assets/tree.jpg',
        //     thumbnail:require('../assets/thumbnail.png'),
        //     way:'自动',
        //     dots:{
        //     leftX:'1796',
        //     leftY:'1698',
        //     upX:'1646',
        //     upY:'177',
        //     rightX:'1888',
        //     rightY:'177',
        //     downX:'1789',
        //     downY:'2248',
        //     }
        // },{
        //     time: '2016-05-02',
        //     treeCode: 'tree111',
        //     diameterCalculate: 11.1,
        //     pic:'../assets/tree.jpg',
        //     thumbnail:require('../assets/thumbnail.png'),
        //     way:'自动',
        //     dots:{
        //     leftX:'1796',
        //     leftY:'1698',
        //     upX:'1646',
        //     upY:'177',
        //     rightX:'1888',
        //     rightY:'177',
        //     downX:'1789',
        //     downY:'2248',
        //     }
        // }]
      }
   },
   mounted(){
      let self = this;
      let treeCode = this.$route.params.treeCode || '';
      self.treeCode = treeCode;
      self.initChart();
      
      if(treeCode && treeCode != ""){
        
         self.fetchData(treeCode);
      }
   },
   methods:{
      showPic(dataIndex,diameter,date){
         console.log('showPic');
      },
      search(){
         this.fetchData(this.treeCode);
      },
      fetchData(treeCode){
         let self = this;
         fetch(`${conf.ROOT}/api/measurements?treeCode=${treeCode}`)
         .then(res => res.json())
         .then(res => {
            self.tableData = res.data;
            let dates = [];
            let diameters = [];
            res.data.forEach(element => {
               dates.push(element.time.split(' ')[0]);
               diameters.push(element.diameterCalculate);
            });
            dates = dates.reverse();
            diameters = diameters.reverse();
            for(let i = 1; i<dates.length;i++){
                if(dates[i-1] == dates[i]){
                    dates.splice(i-1,1);
                    diameters.splice(i-1,1); 
                    i--;                   
                }
            }
            self.treeInfo.measureTimes = dates.length;
            self.tableData = res.data
            self.updateChart(dates,diameters);
         })
         fetch(`${conf.ROOT}/api/trees/${treeCode}`)
         .then(res => res.json())
         .then(res => {
            self.treeInfo.grow = res.data.grow
         })
      },
      updateChart(dates,diameters){
         this.myChart.setOption({
            grid: {
               left: '0%',
               right: '4%',
               containLabel: true
            },
            tooltip: {
               trigger: 'axis'
            },
            xAxis: {
               type : 'category',
               boundaryGap : false,
               axisLine: {onZero: false},
               data: dates
            },
            yAxis: {},
            series: [{
                name: '直径',
                type: 'line',
                data: diameters,
                label: {
                    normal: {
                        show: true,
                        position: 'top'
                    }
                },
            }]
        });
      },
      initChart(){
         let self = this;
         let myChart = this.$echarts.init(document.getElementById('chart'),'macarons')
         myChart.on('click', function(param) {
            self.showPic(param.dataIndex,param.data,param.name);
         });
         self.myChart = myChart;
      }
   }
}
</script>

<style>
.info{
    margin-top: 30px;
    margin-bottom: -40px;
}
.info span{
    margin-right:20px;
}
#chart{
   margin-top:20px;
   width:100%;
   height:400px;
}
</style>



