<template>
  <div class="hello">
    <div class="search-box">
      <el-row>
        <el-date-picker
          v-model="dateRange"
          type="daterange"
          align="right"
          unlink-panels
          value-format="yyyy-MM-dd"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
        <el-button type="primary" @click="search">搜索</el-button>
      </el-row>
      <z-table :tableData="tableData"></z-table>
    </div>
  </div>
</template>

<script>
import Table from './self/Table.vue';
import conf from '../conf.js'
export default {
  // name: 'HelloWorld',
  components:{
    'z-table':Table
  },
  data () {
    return {
      picPath:'',
      msg: 'Welcome to Your Vue.js App',
      dateRange:'',
      detail:{},
      tableData:[{
        time: '2016-05-02',
        treeCode: 'tree111',
        diameterCalculate: 11.1,
        pic:'../assets/tree.jpg',
        thumbnail:require('../assets/thumbnail.png'),
        way:'自动',
        dots:{
          leftX:'1796',
          leftY:'1698',
          upX:'1646',
          upY:'177',
          rightX:'1888',
          rightY:'177',
          downX:'1789',
          downY:'2248',
        }
      }, {
        time: '2016-05-04',
        treeCode: 'tree1111',
        diameterCalculate: 12.2,
        pic:'../assets/tree.jpg',
        thumbnail:'../assets/thumbnail.png',
        way:'自动',
        dots:{
          leftX:'1796',
          leftY:'1698',
          upX:'1646',
          upY:'177',
          rightX:'1888',
          rightY:'177',
          downX:'1789',
          downY:'2248',
        }
      }, {
        time: '2016-05-01',
        treeCode: 'tree111',
        diameterCalculate: 12.5,
        pic:'pic path',
        thumbnail:'/assets/thumbnail.png',
        way:'自动',
        dots:{
          leftX:'1796',
          leftY:'1698',
          upX:'1646',
          upY:'177',
          rightX:'1888',
          rightY:'177',
          downX:'1789',
          downY:'2248',
        }
      }, {
        time: '2016-05-03',
        treeCode: 'tree111',
        diameterCalculate: 13.1,
        pic:'pic path',
        thumbnail:'/assets/thumbnail.png',
        way:'自动',
        dots:{
          leftX:'1796',
          leftY:'1698',
          upX:'1646',
          upY:'177',
          rightX:'1888',
          rightY:'177',
          downX:'1789',
          downY:'2248',
        }
      }]
    }
  },
  mounted(){
    let self = this;
    fetch(conf.ROOT+'/api/measurements')
    .then(res => res.json())
    .then(res => {
      self.tableData = res.data;
    })
  },
  methods:{
    
    search(){
      let dateRange = this.dateRange;
      let self = this;
      fetch(`${conf.ROOT}/api/measurements?beginDate=${dateRange[0]}&endDate=${dateRange[1]}`)
      .then(res => res.json())
      .then(res => {
        self.tableData = res.data;
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.search-box{
  /* padding:20px; */
}

</style>



